import React, {
    useState,
    useCallback,
    useRef,
    useEffect,
    useMemo,
} from 'react';
import T from 'prop-types';
import styled from 'styled-components';
import Tooltip from '@splunk/react-ui/Tooltip';
import { Edit, Remove } from '@splunk/dashboard-icons';
import { _ } from '@splunk/ui-utils/i18n';
import { pick, variables } from '@splunk/themes';

const RemoveIconI18n = () => (
    <Remove screenReaderText={_('Remove')} size="16px" />
);

const Item = styled.div.attrs(({ dataTest, dataTestId }) => ({
    'data-test': dataTest,
    'data-test-id': dataTestId,
}))`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 35px;
    line-height: 30px;
    color: ${variables.contentColorDefault};
    &:hover {
        cursor: ${(prop) => (prop.isItemClickable ? 'pointer' : 'default')};
        background-color: ${variables.backgroundColorHover};
    }
`;

const NameWrapper = styled.div.attrs(({ onClick }) => ({
    'data-test': 'item-name-wrapper',
    'data-clickable': !!onClick,
}))`
    padding-top: 8px;
    width: calc(100% - 70px);
    cursor: ${(props) => (props.onClick ? 'pointer' : 'default')};
`;

const Name = styled.div.attrs(() => ({
    'data-test': 'item-name',
}))`
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: ${(props) => (props.isClickable ? 'pointer' : 'default')};
`;

const Icon = styled.div.attrs(({ type }) => ({
    'data-test': `item-${type}`,
}))`
    width: 35px;
    text-align: center;
    border-radius: ${pick({
        enterprise: '0px',
        prisma: '24px',
    })};
    color: ${variables.contentColorMuted};
    &:hover {
        cursor: pointer;
        color: ${variables.contentColorDefault};
        background-color: ${variables.backgroundColorHover};
    }
`;

const EditableListItem = ({
    name,
    onItemClick,
    onEditClick,
    onRemoveClick,
    itemStyle,
    RemoveIcon,
    'data-test': dataTest,
    'data-test-id': dataTestId,
}) => {
    const [showTooltip, setShowTooltip] = useState(false);
    const timer = useRef(false);

    useEffect(() => {
        return () => {
            if (timer.current) {
                clearTimeout(timer.current);
            }
        };
    });

    const textOverflow = name.length > 15;

    const handleRequestOpen = useCallback(() => {
        clearTimeout(timer.current);
        timer.current = setTimeout(() => setShowTooltip(true), 250);
    }, []);

    const handleRequestClosed = useCallback(() => {
        clearTimeout(timer.current);
        timer.current = setTimeout(() => setShowTooltip(false), 100);
    }, []);

    const editButton = useMemo(() => {
        if (!onEditClick) {
            return null;
        }

        return (
            <Icon type="edit" onClick={onEditClick}>
                <Edit screenReaderText={_('Edit')} size="16px" />
            </Icon>
        );
    }, [onEditClick]);

    const removeButton = useMemo(() => {
        if (!onRemoveClick) {
            return null;
        }

        return (
            <Icon type="remove" onClick={onRemoveClick}>
                <RemoveIcon size="24px" />
            </Icon>
        );
        // Linter doesn't understand that RemoveIcon is not static
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [onRemoveClick, RemoveIcon]);

    return (
        <Item style={itemStyle} dataTest={dataTest} dataTestId={dataTestId}>
            <NameWrapper onClick={onItemClick || null}>
                <Tooltip
                    content={name}
                    open={showTooltip && textOverflow}
                    onRequestOpen={handleRequestOpen}
                    onRequestClose={handleRequestClosed}
                    inline={false}
                >
                    <Name isClickable={!!onItemClick}>{name}</Name>
                </Tooltip>
            </NameWrapper>
            {editButton}
            {removeButton}
        </Item>
    );
};

EditableListItem.propTypes = {
    /**
     * item name
     */
    name: T.string,
    /**
     * callback when click on name
     */
    onItemClick: T.func,
    /**
     * callback when click on edit button
     */
    onEditClick: T.func,
    /**
     * callback when click on delete button
     */
    onRemoveClick: T.func,
    /**
     * Style applied to the item wrapper. Used to add padding to the content
     */
    itemStyle: T.object,
    /**
     * React Element to be used for the remove action. Defaults to X icon
     */
    RemoveIcon: T.elementType,
    'data-test': T.string,
    'data-test-id': T.string,
};

EditableListItem.defaultProps = {
    'data-test': 'editable-list-item',
    itemStyle: { padding: '0 10px' },
    RemoveIcon: RemoveIconI18n,
};

export default EditableListItem;
